.sms-content {
    margin-bottom: 24px;
    text-align: center;

    &__title {
        font-weight: 700;
        font-size: 24px;
        line-height: 32px;
        display: block;
    }

    &__text-wrapper {
        padding: 8px 40px;
        margin-bottom: 24px;
    }

    &__text {
        display: block;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: #959699;
    }

    &__wrapper {
        border-bottom: 1px solid #d5d5d6;
        display: flex;
        justify-content: space-between;
    }

    &__input {
        padding-bottom: 8px;
        font-size: 32px;
        line-height: 40px;
        color: #757575;
        width: 42px;
        height: 58px;

        &::selection {
            background: transparent;
        }
    }

    @media (max-width: 380px) {
        margin-bottom: 16px;

        &__title {
            font-size: 20px;
            line-height: 28px;
        }

        &__text-wrapper {
            padding: 8px 30px;
            margin-bottom: 16px;
        }
    
    }
}