.select {
    &__list {
        margin-bottom: 24px;
    }

    &__list-item{
        &:not(:last-child){
            margin-bottom: 8px;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        padding: 8px 0;
    }

    &__text {
        display: flex;
        flex-direction: column;
        margin-left: 16px;
        margin-right: auto;
    }

    &__icon {
        width: 56px;
        height: 40px;
        display: block;


        &--arrow {
            width: 10px;
            height: 16px;
            transform: rotate(-90deg);

            &.active{
                transform: rotate(90deg);
            }
        }
    }

    &__text-item {
        line-height: 24px;
        color: #2B2D33;
    }
}