.select-card {
    &__text {
        font-size: 14px;
        line-height: 20px;
        color: #AFAFAF;

        &--title {
            font-weight: 600;
            font-size: 18px;
            margin-bottom: 4px;
        }

        &--subtitle {
            font-weight: 400;
            font-size: 16px;
            color: #2B2D33;
        }

        &--desc {
            font-size: 14px;
            line-height: 20px;
            color: #808185;
        }
    }


    @media (max-width: 380px) {
        &__text {
    
            &--title {
                font-weight: 600;
                font-size: 16px;
                color: #2B2D33;
            }
    
            &--subtitle {
                font-size: 14px;
            }
    
        }
    }
}
