html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

ul > li {
  list-style: none;
}

input {
  all: unset;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  margin: 0;
  padding: 0;

  font-family: $default-font;
  font-size: 18px;
  line-height: $line-height;
  font-weight: 400;
  color: #000;

  background: #fff;
}

a {
  text-decoration: none;
}

button,
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}