.alert-modal {
    max-width: 326px;
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #FFFFFF;
    border-radius: 12px;
    padding: 24px 24px 40px 24px;
    line-height: 24px;

    &__title {
        font-family: $semibold-font;
        font-weight: 500;
        font-size: 18px;
        text-align: center;
        color: #000000;
        margin-bottom: 24px;
    }

    &__text {
        font-weight: 400;
        font-size: 16px;
        color: #757575;
        text-align: center;
        margin-bottom: 24px;
    }

    &__icon{
        display: block;
        width: 160px;
        height: 160px;
        margin-bottom: 24px;
        margin: 0 auto;
    }

    &__link {
        font-weight: 600;
        font-size: 18px;
        text-align: center;
        color: #1C83FF;
        padding: 16px;
        width: 100%;
        display: block;
    }
}