.nav{
    display: flex;
    margin-bottom: 24px;
    position: relative;
    overflow: hidden;

    &__link{
        align-items: center;
        font-size: 18px;
        line-height: 24px;
        display: flex;
        color: #000;
    }

    &__icon{
        display: block;
        width: 8px;
        height: 14px;
        margin-right: 12px;
    }

}