.language {
    margin-left: auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    max-width: 160px;
    width: 100%;
    position: relative;
    padding: 0 16px;

    &__list {
        position: absolute;
        right: -50px;
        transition: ease-in-out 0.5s;
    
        &.active {
            right: 30px;
        }
    }

    &__item {
        display: flex;
    }

    &__sublist {
        display: flex;
        visibility: hidden;
        opacity: 0;
        transition: ease-in-out 0.5s;

        &.active{
            visibility: visible;
            opacity: 1;
        }
    }

    &__icon {
        display: block;
        width: 8px;
        height: 14px;
        transform: rotate(-90deg);
        transition: ease-in-out 0.5s;

        &.active{
            transform: rotate(0);
        }
    }

    &__link {
        color: #000;
        padding: 0 6px;
    }

}