.tab {
  margin-bottom: 16px;

  &__list {
    display: flex;
    justify-content: center;
    font-size: 16px;
    line-height: 20px;
    &--flex-start {
      justify-content: flex-start;
      .tab__item {
        width: auto;
        margin: 0;
      }
    }
  }

  &__item {
    margin: auto;
    padding: 10px 18px;
    color: #000000;
    cursor: pointer;

    &.active {
      background: #2b87ff;
      border-radius: 30px;
      color: #fff;
    }
  }

  @media (max-width: 380px) {
    &__list {
      font-size: 14px;
      line-height: 16px;
    }

    &__item {
      padding: 8px 16px;
    }
  }
}