.social-network{
    &__list{
        padding: 0 24px 12px 24px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    &__link{
        padding: 24px;
    }

    &__item{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__icon{
        width: 24px;
        height: 24px;
        display: block;
    }
}