.modal {
    position: absolute;
    left: 0;
    bottom: -100%;
    width: 100%;
    background: #FFFFFF;
    border-radius: 24px 24px 0 0;
    padding: 34px 24px 24px 24px;

    transition: ease-in-out 0.5s;

    &.active {
        bottom: 0;
        z-index: 3;
    }

    &:before {
        content: "";
        display: block;
        background: #000000;
        opacity: 0.1;
        border-radius: 24px;
        width: 34px;
        height: 6px;

        position: absolute;
        top: 12px;
        left: 50%;
        transform: translateX(-50%);
    }

    &__container {
        
    }

    &__title {
        display: block;
        margin-bottom: 24px;
        color: #2B2D33;
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;
    }

    @media (max-width: 380px) {
        padding: 24px 16px 16px 16px;

        &__container {
            padding: 0 16px;
        }

        &__title {
            padding: 0 16px;
            margin-bottom: 16px;
        }

    }
}