.container {
    max-width: 375px;
    width: 100%;
    margin: auto;
    min-height: calc(100dvh - 24px);
    padding: 40px 24px;
    position: relative;
    border-radius: 24px;
    overflow: hidden;
    background: #F0F2F7;

    &::before {
        z-index: -1;
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        filter: blur(1px) opacity(0.8);

        //background: #F0F2F7;
    }


    @media (max-width: 768px){
        min-height: 100dvh;
        max-width: 375px;
    }


    @media (max-width: 380px) {
        padding: 30px 16px;

        margin: 0 auto;
        min-height: 100vh;
    }

}

.overlay {
    background: rgba(0, 0, 0, 0.4);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 1;
    display: none;

    &.active {
        display: block;
    }
}


.loader {
    display: none;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    &.active {
        display: block;
    }

    &__item {
        border: 4px solid #EEEEEE;
        border-top: 4px solid #1C83FF;
        border-radius: 50%;
        width: 34px;
        height: 34px;
        animation: spin 2s linear infinite;
        margin: auto;

        @keyframes spin {
            0% {
                transform: rotate(0deg);
            }

            100% {
                transform: rotate(360deg);
            }
        }
    }

    &__text {
        font-size: 16px;
        line-height: 24px;
        color: #FFFFFF;
        padding: 10px;
    }
}

.social-network{
    display: flex;
    margin: 0 auto;
    padding-top: 10px;
    justify-content: space-between;
    flex-wrap: nowrap;
    flex-direction: row;
    width: 55%;
}