.card {
    &__content {
        position: relative;
        display: flex;

        &--card {
            height: 64px;

            &:not(:last-child) {
                margin-bottom: 16px;
            }
        }


        &--info {
            height: 48px;
        }

        &--phone {
            height: 57px;
            margin-bottom: 16px;

            .card__item:focus~.card__text,
            .card__item:valid~.card__text {
                bottom: 62px;
            }
            &-new {
                height: 64px;
                margin-bottom: 16px;
                margin-top: 0;
            }    
        }

        &--notification {
            height: 64px;
            background: #EDF9F2;
            border-radius: 4px;
            padding: 12px 8px;
        }
    }

    &__notification-text {
        font-size: 14px;
        line-height: 20px;
    }

    &__error {
        font-size: 14px;
        line-height: 20px;
        color: #FB5F5F;
        margin-top: 8px;
        opacity: 0;
        overflow: hidden;
        transition: ease-in-out .3s;

        &.active {
            opacity: 1;
            overflow: visible;
        }
    }

    &__number-wrapper {
        width: 100%;
        height: 100%;
        position: relative;
        display: flex;
        flex-direction: column;

        &--date {
            max-width: 124px;
        }

        &--cvv {
            max-width: 78px;
            margin-left: auto;
        }

        &--info {
            justify-content: center;
            border: none;
        }
    }

    &__text {
        position: absolute;
        bottom: 8px;
        left: 0px;
        width: 100%;
        pointer-events: none;
        transition: 0.2s all;
        font-size: 14px;
        line-height: 20px;
        color: #afafaf;
        transition: all .2s ease;
    }

    &__item {
        border: none;
        color: #2b2d33;
        font-size: 16px;
        line-height: 24px;
        padding-bottom: 8px;
        margin-top: auto;
        border-bottom: 1px solid #D5D5D6;

        &.error{
            border-color: #FB5F5F;
        }
    }

    &__item:invalid {
        outline: 0;
    }

    &__item:focus~&__text,
    &__item:valid~&__text,
    &__item:disabled~&__text,
    &__item:not(:placeholder-shown)~&__text,
    &__item#card-phone~&__text {
        bottom: 36px;
    }


    &__checkbox-input {
        display: none;
    }

    &__checkbox-label {
        font-size: 16px;
        line-height: 24px;
        color: #2B2D33;
        display: flex;
        align-items: center;
        padding-left: 64px;
    }

    &__checkbox-input:checked+&__checkbox-label::before {
        background-color: #1C83FF;
    }

    &__checkbox-label::before {
        background: #ccc;
        background-color: rgb(204, 204, 204);
        border: 2px solid #e5e5ea;
        border-radius: 36px;
    }

    &__checkbox-label::after,
    &__checkbox-label::before {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        content: '';
        display: block;
        width: 34px;
        height: 14px;
        transition: all .3s ease-in-out;
    }

    &__checkbox-input:checked+&__checkbox-label::after {
        left: 15px;
    }

    &__checkbox-label::after {
        left: 0px;
        width: 20px;
        height: 20px;
        background-color: #fff;
        border: .5px solid rgba(0, 0, 0, .04);
        box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 1px rgba(0, 0, 0, .16), 0 3px 8px rgba(0, 0, 0, .15);
        border-radius: 50%;
    }

    &__info-text {
        font-size: 12px;
        line-height: 16px;
        text-align: center;
        margin: auto 0;
    }

    &__info-link {
        cursor: pointer;
        color: #1C83FF;

        &:hover {
            text-decoration: underline;
        }
    }

    @media (max-width: 380px) {
        &__checkbox-label {
            font-size: 14px;
            line-height: 16px;
        }
    }

}