.button {
    background: #E2E2E2;
    border-radius: 8px;
    height: 56px;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #757575;
    cursor: pointer;
    max-width: 280px;
    width: 100%;
    margin: 0 auto;
    display: block;
    border: none;

    &--mb24{
        margin-bottom: 24px;
    }

    &:not(:last-child){
        margin-bottom: 16px;
    }

    &.active {
        background: #1C83FF;
        color: #fff;
    }

    @media (max-width: 380px) {
        max-width: 256px;
        font-size: 14px;

        &--mb24{
            margin-bottom: 16px;
        }

        &:not(:last-child){
            margin-bottom: 12px;
        }
    }
}