.wallet-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    padding: 16px 0;
    border-bottom: 1px solid #E9E9E9;
    margin-bottom: 24px;

    &__text {
        display: block;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        color: #757575;

        &--label {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: #AFAFAF;
        }
    }
}