.alert {
    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 32px;
    padding-right: 32px;
    background-color: #45BF76;
    box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    margin-bottom: 24px;

    @media (max-width: 380px) {
        padding-left: 16px;
        padding-right: 16px;
        margin-bottom: 16px;
    }

    &--error {
        background-color: #FB5F5F;
    }

    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    &__icon {
        width: 48px;
        height: 48px;
        display: block;
        margin-bottom: 24px;

        &--main {
            margin-bottom: 12px;
            width: 40px;
            height: 40px;
        }
    }

    &__text {
        color: #FFFFFF;
        font-size: 14px;
        line-height: 20px;

        &--title {
            font-weight: 600;
            font-size: 18px;
            line-height: 24px;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        cursor: pointer;
    }

    &__footer {
        display: flex;
        justify-content: space-around;
        margin-top: 24px;
    }
}