.content-info {
    background-color: #fff;
    border-radius: 8px;
    margin-bottom: 15px;

    &__button {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 24px;
        cursor: pointer;


        @media (max-width: 380px) {
            padding: 16px;
        }
    }

    &__arrow {
        display: block;
        width: 10px;
        height: 16px;
        background-image: url("../../../images/svg/arrow.svg");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        transform: rotate(-90deg);

        &.active {
            transform: rotate(90deg);
        }
    }

    &__button-text {
        display: flex;
        flex-direction: column;
    }

    &__text {
        color: #757575;
        font-size: 16px;
        line-height: 18px;
        display: table-cell;

        &--label {
            width: 158px;
        }

        &--value {
            color: #000;
        }

        &--bold {
            font-weight: 600;
        }

        @media (max-width: 380px) {
            font-size: 14px;
            line-height: 16px;

            &--label {
                width: 126px;
            }
        }
    }

    &__amount {
        font-family: $bold-font;
        font-size: 24px;
        line-height: 18px;
        margin-top: 8px;

        
        @media (max-width: 380px) {
            font-size: 20px;
        }
    }


    &__more-info {
        padding-bottom: 24px;
        padding-left: 24px;
        padding-right: 24px;
        padding-top: 0;
    }

    &__list {
        border-top: 1px solid #e9e9e9;
        padding-top: 24px;
    }

    &__row {
        &:not(:last-child) {
            margin-bottom: 24px;

            @media (max-width: 380px) {
                margin-bottom: 16px;
            }
        }
    }
    //&--united {
    //    border-bottom-left-radius: 0;
    //    border-bottom-right-radius: 0;
    //}
}